*{ 
  margin: 0; 
  padding: 0; 
}

a {
  color: #FFF;
}
a:hover {
   color: rgb(70, 14, 143)
}


.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: right;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  top: 0;
}

.App-link {
  color: rgba(18, 233, 215, 0.973);
}

.nav-bar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #282c34;
}


.home-paragraph {
  margin-top: 50px;
}

.home-headshot {
  float:left;
  margin-left: 50px;

}
